import React, { Component } from 'react';
import debounce from 'lodash';
import $ from 'jquery';
import Menu from './Menu';
import Header from './Header';
import Second from './Second';
import Services from './Services';
import Footer from './Footer';

//STYLE
import './utils/scss/main.scss';

//SCRIPT
//import Scroll from './utils/js/Scroll';
import Animate from './utils/js/Animate';

export default class Main extends Component {
    componentDidMount() {
        $('img.svg').each(function(){
            var $img = $(this)
            var imgID = $img.attr('id')
            var imgClass = $img.attr('class')
            var imgURL = $img.attr('src')
        
            $.get(imgURL, function(data) {
                var $svg = $(data).find('svg')                 
                if (typeof imgID !== 'undefined') {
                    $svg = $svg.attr('id', imgID)
                }
                
                if (typeof imgClass !== 'undefined') {
                    $svg = $svg.attr('class', imgClass+' replaced-svg')
                }
                 
                $svg = $svg.removeAttr('xmlns:a')
                 
                $img.replaceWith($svg)
            })
        })

        let animate = new Animate()
        const target = document.querySelectorAll('[data-animate]')
        if (target.length) {
            window.addEventListener('scroll', () => debounce(animate.animateScroll(), 1000))
        }

        let icon = $('.menu')
        icon.click(function() {
            icon.toggleClass('ex')
        })

        window.onload = () => $('#preload').delay(1000).fadeOut(800)
    }

    render() {
        return(
            <div>
                <div id="preload" className="preload"></div>
                <Menu />
                <div className="container">
                    <Header />
                </div>
                <div className="container">
                    <Second />
                </div>
                <div className="container">
                    <Services />
                </div>
                <Footer />
            </div>
        )
    }
}